const userAgent = window.navigator.userAgent
const system = (userAgent.indexOf('iPhone') > -1 || userAgent.indexOf('iPad') > -1) ? 'iPhone' : 'Android'
const appVersion = userAgent.split('YJEJ')[1] && userAgent.split('YJEJ')[1].split('/')[1]
const deviceEnv = userAgent.match(/(iPhone|iPod|iPad|Android|ios)/i) ? 'mobile' : 'pc'
const isWechat = /MicroMessenger/i.test(navigator.userAgent.toLowerCase())
let iosModel
if (appVersion && system === 'iPhone') {
  iosModel = window.webkit.messageHandlers.iOSModel
}
// 接口请求地址
let httpUrl
if (process.env.NODE_ENV === 'development') {
  httpUrl = '/api/'
} else {
  httpUrl = '/'
}

export {
  httpUrl,
  system,
  appVersion,
  iosModel,
  deviceEnv,
  isWechat
}
