<template>
  <div id="app">
    <router-view/>
    <loading :message="message" v-show="isShow"/>
  </div>
</template>

<script>
  import Vue from 'vue'
  import Loading from './components/Loading'
  import { appVersion, iosModel } from './config/base'
  export default {
    data () {
      return {
        isShow: false, // 是否显示loading
        message: '加载中...' // loading默认文案
      }
    },
    created () {
      // 暴露全局方法
      let myVue = Vue.prototype
      myVue.showToast = this.showToast
      myVue.showLoading = this.showLoading
    },
    methods: {
      showToast (obj) {
        if (appVersion) {
          if (window.inv && window.inv.showToast) {
            window.inv.showToast(obj.message)
          } else {
            if (iosModel) {
              iosModel.postMessage({ 'action': 'showToast', params: JSON.stringify({ message: obj.message }) })
            }
          }
        } else {
          let toast = this.$createToast({ txt: obj.message, type: 'txt', time: 1500, mask: obj.mask })
          toast.show()
        }
      },
      showLoading (isShow, message = '加载中...') {
        this.message = message
        if (appVersion) {
          if (isShow) {
            if (window.inv && window.inv.showLoadingDialog) {
              window.inv.showLoadingDialog(message)
            } else {
              if (iosModel) {
                iosModel.postMessage({ 'action': 'showLoadingDialog', params: JSON.stringify({ message: message }) })
              }
            }
          } else {
            if (window.inv && window.inv.dismissLoadingDialog) {
              window.inv.dismissLoadingDialog()
            } else {
              if (iosModel) {
                iosModel.postMessage({ 'action': 'dismissLoadingDialog' })
              }
            }
          }
        } else {
          this.isShow = isShow
        }
      }
    },
    components: {
      Loading
    }
  }
</script>

<style lang="less">
  @import "styles/index";
</style>
