import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './types'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userInfo: {},
    userInfoReady: false
  },
  getters: {
    userInfo: state => state.userInfo,
    userInfoReady: state => state.userInfoReady
  },
  mutations: {
    [types.USERINFO_READY] (state, ready) {
      state.userInfoReady = ready
    },
    [types.USERINFO_CONTENT] (state, userInfo) {
      state.userInfo = userInfo
    }
  },
  actions: {},
  modules: {}
})
