import Vue from 'vue'
import { httpUrl, appVersion, iosModel } from './base'
import { parseQuery } from './utility'
import axios from 'axios'
import router from '../router'
import store from '../store'
let myVue = Vue.prototype
// responseType 表示服务器响应的数据类型，可以是 'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
// axios.defaults.responseType = 'json'
// // `withCredentials` 表示跨域请求时是否需要使用凭证
axios.defaults.withCredentials = true
// 如果请求话费了超过 `timeout` 的时间，请求将被中断
axios.defaults.timeout = 60000
// 请求响应的拦截
axios.interceptors.response.use((response) => {
  // 在这个地方做接口拦截处理
  return response
}, (err) => {
  // console.log(err)
  const error = err.response
  if (err.code === 'ECONNABORTED') {
    return Promise.resolve({
      status: 500,
      error: '网络繁忙，请稍后重试'
    })
  } else {
    return Promise.resolve(error)
  }
})

export default function fetch (params, options = {}) {
  let newOption = { mask: true, pop: true, isCatch: false, message: '加载中...', method: 'POST', responseType: 'json', isUpload: false, ...options }
  let { mask, pop, isCatch, message, method, responseType, isUpload } = newOption
  let token = parseQuery(window.location.hash)['token'] || localStorage.getItem('token')
  // 如果存在token，就先保存
  if (token) {
    localStorage.setItem('token', token)
  }
  let headers = token ? { Authorization: 'Bearer ' + token } : {}
  if (isUpload) {
    headers = { 'Content-Type': 'multipart/form-data', ...headers }
  }
  let url = params['url']
  delete params['url']
  if (appVersion) {
    if (pop && window.inv && window.inv.showLoadingDialog) {
      window.inv.showLoadingDialog(message)
    } else {
      pop && iosModel && iosModel.postMessage({ 'action': 'showLoadingDialog', params: JSON.stringify({ message: message }) })
    }
  } else {
    setTimeout(() => { pop && myVue.showLoading(true, message) }, 0)
  }
  return new Promise((resolve, reject) => {
    axios({
      url: httpUrl + url,
      headers: headers,
      data: params,
      method: method,
      responseType: responseType
    }).then(function (res) {
      // console.log(res)
      if (res.status === 200) {
        let data = res.data
        if (data.code === 0) {
          // 接口正常
          resolve(data)
        } else {
          if (isCatch) {
            reject(data)
          } else {
            setTimeout(() => { myVue.showToast({ message: data['message'], mask }) })
          }
        }
      } else if (res.status === 401) {
        // 用户未登录的时候，去到登录页面
        // 移除用户信息
        localStorage.removeItem('token')
        store.commit('USERINFO_CONTENT', {})
        // 如果是用户信息接口
        if (res.config.url.indexOf('my/profile') !== -1) {
          if (isCatch) {
            reject(res.data)
          }
        } else {
          if (appVersion) {
            // 在APP调用登录的方法
            if (window.inv && window.inv.login) {
              window.inv.login()
            } else {
              iosModel && iosModel.postMessage({ 'action': 'login' })
            }
          } else {
            // 跳转到登录页面
            router.push({ path: 'stepone' })
          }
        }
      } else {
        let err = res.data || res
        let msg = err ? err['error'] : res.statusText
        if (isCatch) {
          reject(err)
        } else {
          setTimeout(() => { myVue.showToast({ message: msg, mask }) })
        }
      }
    }).finally(() => {
      if (appVersion) {
        if (pop && window.inv && window.inv.dismissLoadingDialog) {
          window.inv.dismissLoadingDialog()
        } else {
          pop && iosModel && iosModel.postMessage({ 'action': 'dismissLoadingDialog' })
        }
      } else {
        setTimeout(() => { pop && myVue.showLoading(false) }, 0)
      }
    })
  })
}
