import Vue from 'vue'
let myVue = Vue.prototype

// 校验器
const validation = {
  // 手机号码
  isPhone (value) {
    return /^1[3-9]\d{9}$/.test(value)
  }
}
myVue.validation = validation

// url参数获取的方法
function parseQuery (url) {
  let obj = {}
  let keyValue = []
  let key, value
  let paraString = url.split('?')[1] && url.split('?')[1].split('&')
  for (let i in paraString) {
    keyValue = paraString[i].split('=')
    key = keyValue[0]
    value = keyValue[1]
    obj[key] = value
  }
  return obj
}
myVue.parseQuery = parseQuery

export {
  parseQuery
}
